@media (max-width: 910px) {
  .introduction-wrapper {
    .content {
      max-width: 500px;
    }

    .hey-there {
      font-size: 24px;
    }

    .name {
      font-size: 100px;
    }

    .description {
      font-size: 20px;
      max-width: 350px;
    }

    .decorations {

      .circle {
        width: 130px;
      }

      .saas {
        width: 120px;
      }

      .non-code {
        width: 120px;
      }
    }
  }
}
@media (max-width: 744px) {
  .introduction-wrapper {
    .content {
      max-width: 500px;
    }

    .hey-there {
      font-size: 24px;
    }

    .name {
      font-size: 100px;
    }

    .description {
      font-size: 20px;
      max-width: 350px;
    }

    .decorations {

      .circle {
        display: none;;
      }

      .saas {
        display: none;
      }

      .non-code {
        display: none;
      }
    }
  }
}
@media (max-width: 500px) {
  .introduction-wrapper {
    .introduction {
      padding: 80px 40px;
    }

    .hey-there {
      font-size: 20px;
      line-height: 45px;
    }

    .name {
      font-size: 70px;
    }

    .description {
      font-size: 16px;
      max-width: 350px;
    }

    .decorations {
      .star-1 {
        left: 20px;
      }

      .star-2 {
        left: 30px;
      }

      .circle {
        display: none;
      }

      .saas {
        display: none;
      }

      .non-code {
        display: none;
      }
    }
  }
}